body {
  font-family: var(--main-font);
  color: #fcfcfc;
  cursor: url("cursor.d8582cc2.svg"), pointer;
  font-size: 16px;
  line-height: 1.75;
}

.cursor {
  width: 31px;
  height: 31px;
  border: 1px solid var(--clr-brand-darkest);
  pointer-events: none;
  border-radius: 50%;
  transition-duration: .2s;
  transition-timing-function: ease-out;
  animation: cursorAnim .5s infinite alternate;
  position: absolute;
  top: -8px;
  left: -8px;
}

.cursor:after {
  content: "";
  width: 30px;
  height: 30px;
  border: 8px solid var(--clr-accent);
  opacity: .5;
  border-radius: 50%;
  animation: cursorAnim2 .5s infinite alternate;
  position: absolute;
}

@keyframes cursorAnim {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(.7);
  }
}

@keyframes cursorAnim2 {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(.4);
  }
}

@keyframes cursorAnim3 {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(3);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

